.wrapper {
    font-family: 'Source Code Pro', monospace;
    background-color: rgba(18, 18, 26,1);

    h2 {
        font-size: calc(14px + (18 - 14) * ((100vw - 270px) / (1600 - 270)));
        font-weight: 700;
        text-shadow: 0 3px 5px rgba(0,0,0,0.5);
        text-align: center;
    }
}

.squareContainer {
    width: 100%;
    height: 40vh;
}

.squareContainerV {
    width: 100%;
}

.sakuraBana {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, rgba(0,0,0,0) 43%, rgba(0,0,0,0.5) 80%), url(../assets/FeaturedImages/sakura-bana-food-v2.jpeg); 
    background-size: cover; 
    background-position: center;

    .foodImg {
        height: 50vh;

        background-image: url(../assets/FeaturedImages/sakura-bana-food.jpeg); 
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
}

.spezia {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, rgba(0,0,0,0) 43%, rgba(0,0,0,0.5) 80%), url(../assets/FeaturedImages/spezia-food.jpeg); 
    background-size: cover; 
    background-position: center; 
    
    .foodImg {
        height: 50vh;

        background-image: url(../assets/FeaturedImages/spezia-location.jpeg); 
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
