    .foodImg {
        height: 50vh;

        background-image: url(../assets/FeaturedImages/los-tapatios-food.jpeg); 
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
}

.earlyBird {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, rgba(0,0,0,0) 43%, rgba(0,0,0,0.5) 80%), url(../assets/FeaturedImages/early-bird-location.jpeg); 
    background-size: cover; 
    background-position: center; 
    
    @media (max-width: 30em) {
        background-image: linear-gradient(180deg, rgba(0,0,0,0.5) 43%, rgba(0,0,0,0.5) 80%), url(../assets/FeaturedImages/early-bird-location.jpeg); 
    }

    .foodImg {
        height: 50vh;

