.blueSushi {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, rgba(0,0,0,0) 43%, rgba(0,0,0,0.5) 80%), url(../assets/FeaturedImages/blue-location.jpg); 
    background-size: cover; 
    background-position: center;  
    
    @media (max-width: $breakPoint) {
        background-image: linear-gradient(180deg, rgba(0,0,0,0.5) 43%, rgba(0,0,0,0.5) 80%), url(../assets/FeaturedImages/blue-location.jpg); 
