.info {
    position: fixed;
    height: 40vh;
}

.btnDisplay {

    .whitebtn {
        font-size: .8em;
        color: $white;
        border: 1px solid $white;
        display: inline-block;
        padding: 8px 22px;
        margin-top: 20px;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 2px;
        transition: 0.5s;
    
        &:hover {
            background-color: $background;
            border: 1px solid $background;
            letter-spacing: 4px;
        }
    }

    .redbtn {
        font-size: 1em;
        color: #fff;
        background-color: $primaryColor;
        display: inline-block;
        padding: 4px 14px;
        margin-top: 20px;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 2px;
        transition: 0.5s;

        &:hover {
            letter-spacing: 4px;
        }
    }
}